.hero {
    background: var(--accent-color);
    background-size: cover;
    background-position: center;
    color: var(--primary-color);
    padding: 4rem 0;
    min-height: calc(100vh - 76px);
    display: flex;
    align-items: center;
}

.hero img {
    border: 5px solid white;
    transition: transform 0.3s ease;
    max-width: 100%;
    height: auto;
}

.hero img:hover {
    transform: scale(1.02);
}

.hero h1 {
    color: var(--primary-color);
    margin-bottom: 1rem;
}

.hero h2 {
    color: var(--dark-color);
    opacity: 0.9;
}

.hero .lead {
    font-size: 1.25rem;
    margin-bottom: 2rem;
}

.hero .btn-outline-light {
    color: var(--primary-color);
    border-color: var(--primary-color);
}

.hero .btn-outline-light:hover {
    background-color: var(--primary-color);
    color: var(--light-color);
}

/* Priority Cards */
.card {
    border: none;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0,0,0,0.15);
}

.card-title {
    color: var(--primary-color);
    margin-bottom: 1rem;
}

.card-text {
    color: var(--dark-color);
    opacity: 0.9;
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero {
        text-align: center;
        padding: 3rem 0;
    }

    .hero img {
        margin-top: 2rem;
        max-width: 80%;
        margin-left: auto;
        margin-right: auto;
    }

    .hero .btn {
        display: block;
        width: 100%;
        margin-bottom: 1rem;
    }

    .hero .btn-lg {
        margin-right: 0 !important;
    }
}


/* ================================ */
/* Embedded YouTube Video Section   */
/* ================================ */
.video-section {
    background: #f7f7f7; /* or any color you prefer */
    padding: 3rem 1rem;
  }
  
  .video-section .text-center.mb-4 {
    margin-bottom: 2rem;
  }
  
  /* The iframe wrapper for responsive sizing */
  .video-wrapper {
    position: relative;
    width: 100%;
    max-width: 800px; /* keeps video from getting too wide on large screens */
    margin: 0 auto; /* center horizontally */
    padding-bottom: 56.25%; /* ratio for 16:9 aspect ratio */
    height: 0;
    overflow: hidden;
  }
  
  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
  
  /* Media query for smaller screens */
  @media (max-width: 768px) {
    .video-section {
      padding: 2rem 1rem;
    }
  
    .video-wrapper {
      max-width: 100%;
      padding-bottom: 56.25%; /* maintain 16:9 ratio */
    }
  }
